import { createStore } from "vuex";

export default createStore({
  state: {
    array: [],
    arrayHoteles: [],
    arraySabanas: []
  },

  mutations: {
    setArray(state, payload) {
      state.array = payload.data;
      state.arrayHoteles = payload.hoteles;
    }
  },

  actions: {
    // OBTIENE DATOS DEL ARRAY
    async getArray({ commit }, array) {
      try {
        const res = await fetch(`https://maphg.com/america/apis/sabanas.php`, {
          // const res = await fetch(`https://maphg.com/america/apis/sabanas.php`, {
          method: "POST",
          body: JSON.stringify(array)
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          commit("setArray", data);
          return { response: true, data: data };
        }

        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },

    // OBTIENE DATOS DEL ARRAY
    async accionesUsuario({ commit }, array) {
      try {
        const res = await fetch(`https://maphg.com/america/apis/sabanas.php`, {
          // const res = await fetch(`https://maphg.com/america/apis/sabanas.php`, {
          method: "POST",
          body: JSON.stringify(array)
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return { response: true, data: data };
        }
        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },

    // OBTIENE DATOS DEL ARRAY
    async sesion({ commit }, array) {
      try {
        const res = await fetch(
          `https://maphg.com/america/apis/inicio_sesion.php`,
          {
            // await fetch(`https://maphg.com/america/apis/inicio_sesion.php`, {
            method: "POST",
            body: JSON.stringify(array)
          }
        );
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return { response: true, data: data };
        }
        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },

    // SUBE FOTO DE USUARIO
    async subirAdjunto({ commit }, array) {
      try {
        const res = await fetch(`https://maphg.com/america/apis/adjuntos.php`, {
          // fetch(`https://maphg.com/america/apis/adjuntos.php`, {
          method: "POST",
          body: array
        });
        const data = await res.json();
        if (data.resp === "SUCCESS") return { resp: true, data: data };
        else return false;
      } catch (error) {
        console.log(error);
        return false;
      }
    }
  }
});
