<template>
  <div class="
      w-full
      h-screen
      bg-gris-30
      flex flex-col
      items-center
      justify-start
      py-2
    ">
    <!-- DATOS DEL USUARIO -->
    <div class="w-full flex flex-row justify-center items-center relative">
      <h1 class="font-semibold uppercase mr-1">User:</h1>
      <strong> {{ nombreUsuario }} </strong>
      <LogoutIcon @click="$router.push('/login')"
        class=" w-8 h-8 text-gris-200 absolute -top-3 right-1 cursor-pointer bg-gris-800 rounded-full p-1.5 my-2 " />
    </div>

    <!-- TÍTULO -->
    <div class="p-4" v-if="mostrar">
      <h1 class="font-bold uppercase">Checklist</h1>
    </div>

    <!-- SELECT HOTEL -->
    <div class="px-4 pb-4 w-full" v-if="mostrar">
      <select @change="consultarTiposActivos()" v-model="datos.idHotel"
        class="px-2 py-3 rounded-lg focus:outline-none w-full">
        <option value="0">Hotel</option>
        <option v-for="(item, index) in array.hoteles" :key="index" :value="item.idHotel">
          {{ item.hotel }}
        </option>
      </select>
    </div>

    <!-- SELECT TIPO DE ACTIVO -->
    <div class="px-4 pb-4 w-full" v-if="mostrar">
      <select v-model="datos.tipoActivo" @change="consultarCheckList()"
        class="px-2 py-3 rounded-lg focus:outline-none w-full">
        <option value="0">Tipo / Type</option>
        <option v-for="(item, index) in array.tiposActivos" :key="index" :value="{
          idTipoActivo: item.idTipoActivo,
          idRegistroTipoActivo: item.idRegistroTipoActivo,
        }">
          {{ item.tipo }}
        </option>
      </select>
    </div>

    <!-- SELECT DE CHECKLIST -->
    <div class="px-4 pb-4 w-full" v-if="mostrar">
      <select class="px-2 py-3 rounded-lg focus:outline-none w-full" v-model="datos.idCheckList"
        @change="consultarActivos()">
        <option value="0">CheckList</option>
        <option v-for="(item, index) in array.checkList" :key="index" :value="item.idSabana">
          {{ item.sabana }}
        </option>
      </select>
    </div>

    <!-- SELECT DE EQUIPO -->
    <div class="px-4 pb-4 w-full" v-if="mostrar">
      <select class="px-2 py-3 rounded-lg focus:outline-none w-full" v-model="datos.idActivo">
        <option value="0">Habitación / Room</option>
        <option v-for="(item, index) in array.activos" :key="index" :value="item.idEquipo">
          {{ item.equipo }}
        </option>
      </select>
    </div>

    <!-- BOTÓN CREAR SÁBANAS -->
    <div class="px-4 pb-4 w-full">
      <button v-if="mostrar" @click="crearRegistro()" class="
          px-2
          py-3
          rounded-lg
          focus:outline-none
          w-full
          bg-bluedemon-500
          text-white
        ">
        Crear / Create
      </button>
    </div>

    <!-- LISTA DE CHECKLIST -->
    <div v-for="(item, index) in arraySabana" :key="index"
      class="px-4 pb-4 w-full flex flex-col items-center justify-start">
      <div class="mb-4">
        <h1 class="font-bold uppercase">{{ item.sabana }}</h1>
      </div>

      <!-- Listado -->
      <div class="
          flex flex-col
          items-center
          justify-start
          w-full
          shadow
          rounded-xl
        ">
        <div class="
            flex flex-col
            items-center
            justify-start
            w-full
            shadow
            rounded-xl
          ">
          <Apartadosabanas v-for="(item, index) in item.apartados" :key="index" :array="item" :crear="datos" />
        </div>
      </div>

      <!-- OPCIONES -->
      <div class="px-4 pb-4 w-full">
        <button class="
            px-2
            py-3
            rounded-lg
            focus:outline-none
            w-full
            bg-green-500
            text-white
            mt-4
          " @click="confirmarRegistro()">
          Finalizar / Finish
        </button>

        <button class="
            px-2
            py-3
            rounded-lg
            focus:outline-none
            w-full
            bg-red-500
            text-white
            mt-4
          " @click="$router.go($router.currentRoute)">
          Cancelar / Cancel
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { ref } from "@vue/reactivity";
import Apartadosabanas from "../components/Apartadosabanas.vue";
import { onMounted } from "@vue/runtime-core";
import { useStore } from "vuex";
import router from "@/router";
import uniqid from "uniqid";
import { format } from "date-fns";

import { LogoutIcon } from "@heroicons/vue/outline";

export default {
  components: { Apartadosabanas, LogoutIcon },
  props: {},
  setup() {
    const store = useStore();
    const Swal = require("sweetalert2");

    const nombreUsuario = ref(" - - - ");
    const mostrar = ref(true);

    const array = ref({
      hoteles: [],
      tiposActivos: [],
      checkList: [],
      activos: [],
    });

    const datos = ref({
      idHotel: "0",
      tipoActivo: "0",
      idCheckList: "0",
      idActivo: "0",
      idRegistro: "",
      totalActividades: "0",
    });

    const arraySabana = ref([]);

    onMounted(async () => {
      const sesion = await store.dispatch("sesion", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "comprobarSesion",
      });

      if (sesion.response) {
        nombreUsuario.value = sesion.data.data.nombre;
        consultarHoteles();
        return;
      }

      if (!sesion.response) {
        router.push("login");
        return;
      }
    });

    const consultarHoteles = async () => {
      array.value.hoteles = [];

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "consultarHoteles",
      });

      if (response.response) {
        array.value.hoteles = response.data.data;
      }
    };

    const consultarTiposActivos = async () => {
      array.value.tiposActivos = [];
      array.value.checkList = [];
      array.value.activos = [];

      datos.value.tipoActivo = 0;
      datos.value.idCheckList = 0;
      datos.value.idActivo = 0;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        idSabana: datos.value.idCheckList,
        action: "consultarTiposActivos",
        idHotel: datos.value.idHotel,
      });

      if (response.response) {
        if (response.data.data.length) {
          array.value.tiposActivos = response.data.data;
          return;
        }
      }
    };

    const consultarCheckList = async () => {
      array.value.checkList = [];
      array.value.activos = [];

      datos.value.idCheckList = 0;
      datos.value.idActivo = 0;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        idHotel: datos.value.idHotel,
        idRegistroTipoActivo: datos.value.tipoActivo.idRegistroTipoActivo,
        action: "consultaSabanas",
      });

      if (response.response) {
        if (response.data.data.length) {
          array.value.checkList = response.data.data;
          return;
        }
      }
    };

    const consultarActivos = async () => {
      datos.value.idActivo = 0;

      array.value.activos = [];

      if (datos.value.idCheckList == 0) return;

      const data = {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "consultarEquipos",
        idSabana: datos.value.idCheckList,
        idHotel: datos.value.idHotel,
        idTipoActivo: datos.value.tipoActivo.idTipoActivo,
      };

      const response = await store.dispatch("accionesUsuario", data);

      if (response.response) {
        if (response.data.data.length) {
          array.value.activos = response.data.data;
          return;
        }
      }
    };

    const crearRegistro = async () => {
      const idRegistro = uniqid() + uniqid();
      datos.value.idRegistro = idRegistro;

      if (datos.value.idCheckList == 0) return;
      if (datos.value.idActivo == 0) return;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        idSabana: datos.value.idCheckList,
        idEquipo: datos.value.idActivo,
        idRegistro: idRegistro,
        action: "crearRegistro",
        fechaCreado: format(new Date(), "yyyy-MM-dd K:m:s"),
      });

      if (response.response) {
        mostrar.value = false;
        arraySabana.value = response.data.data;
        datos.value.totalActividades = response.data.totalActividades;
      }

      if (!response.response) {
        arraySabana.value = [];
        mostrar.value = true;
      }
    };

    const confirmarRegistro = async () => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        idSabana: datos.value.idCheckList,
        idEquipo: datos.value.idActivo,
        idRegistro: datos.value.idRegistro,
        totalActividades: datos.value.totalActividades,
        action: "confirmarRegistro",
        fechaFinalizado: format(new Date(), "yyyy-MM-dd K:m:s"),
      });

      if (response.response) {
        if (response.data.completado) {
          mostrar.value = true;
          arraySabana.value = [];

          Swal.fire({
            title: "Actividades Finalizadas!",
            html: `Se han Completado el 100%`,
            icon: "success",
            background: "#2c3e50",
            customClass: {
              popup: "colored-toast",
              backdrop: "swal2-backdrop-show",
              icon: "swal2-icon-show",
            },
            iconColor: "#fff",
          });
          return;
        }

        if (!response.data.completado) {
          const porcentaje = datos.value.totalActividades > 0 && response.data.restantes < datos.value.totalActividades ? ((100 / datos.value.totalActividades) * response.data.restantes).toFixed(2) : 100;

          const mensaje = porcentaje == 0 ? "¡Foto y Comentario Obligatorio!" : `Faltan por Completar el ${porcentaje}% `;

          Swal.fire({
            title: "Actividades Incompletas!",
            html: mensaje,
            icon: "info",
            background: "#2c3e50",
            customClass: {
              popup: "colored-toast",
              backdrop: "swal2-backdrop-show",
              icon: "swal2-icon-show",
            },
            iconColor: "#fff",
          });
          return;
        }
      }
    };

    return {
      mostrar,
      array,
      consultarTiposActivos,
      consultarCheckList,
      consultarActivos,
      arraySabana,
      datos,
      crearRegistro,
      confirmarRegistro,
      nombreUsuario,
    };
  },
};
</script>
<style scoped>

</style>
