<template>
  <div class="actividad relative">
    <div class="w-1 h-1 rounded-full bg-black mr-2 flex-none"></div>
    <h1 class="">{{ array.apartado }}</h1>

    <!-- OPCIONES -->
    <div v-if="array.opciones === 'SI'" class="flex flex-row justify-end items-center w-1/2 absolute right-0 px-0">
      <!-- BOTÓN SI -->
      <button @click="valorCrear = 'SI'" class="bg-green-200 text-green-500" v-if="toggleOpciones">
        SI/YES
      </button>

      <!-- BOTÓN NO -->
      <button @click="valorCrear = 'NO'" v-if="toggleOpciones" class="bg-yellow-200 text-yellow-500">
        NO
      </button>

      <!-- BOTÓN N/A -->
      <button @click="valorCrear = 'N/A'" v-if="toggleOpciones" class="bg-naranja1a-200 text-naranja1a-500">
        N/A
      </button>

      <!-- BOTÓN OPCIONES -->
      <div
        @click="
          toggleOpciones = !toggleOpciones;
          valorCrear = '';
        "
        class="text-gray-700 pr-2 cursor-pointer"
      >
        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
          <path d="M10 6a2 2 0 110-4 2 2 0 010 4zM10 12a2 2 0 110-4 2 2 0 010 4zM10 18a2 2 0 110-4 2 2 0 010 4z" />
        </svg>
      </div>
    </div>
  </div>

  <Actividadsabana v-for="(item, index) in array.actividades" :key="index" :array="item" :crear="crear" :index="index" :valorCrear="valorCrear" />
</template>

<script>
  import Actividadsabana from "../components/Actividadsabana.vue";

  import { ref } from "@vue/reactivity";
  export default {
    components: { Actividadsabana },

    props: {
      crear: { type: Object, required: false },
      array: { type: Object },
    },

    setup() {
      const toggleOpciones = ref(false);
      const valorCrear = ref("ok");

      return { toggleOpciones, valorCrear };
    },
  };
</script>

<style scoped>
  .actividad {
    @apply flex flex-row flex-wrap items-center justify-start w-full py-2 bg-gray-200 px-2 text-sm text-center font-bold border-b uppercase;
  }

  .actividad:hover {
    @apply bg-blue-100;
  }

  .actividad:last-child {
    @apply border-b-0 rounded-b-xl;
  }

  .actividad:first-child {
    @apply rounded-t-xl;
  }

  button {
    @apply p-2 rounded mr-2 font-bold w-auto px-3 flex items-center justify-center focus:outline-none;
  }

  button:last-child {
    @apply mr-0;
  }

  .bg-naranja1a-200 {
    --tw-bg-opacity: 1;
    background-color: rgba(255, 231, 198, var(--tw-bg-opacity));
  }

  .border-naranja1a-500 {
    --tw-border-opacity: 1;
    border-color: rgba(255, 159, 26, var(--tw-border-opacity));
  }

  .text-naranja1a-500 {
    --tw-text-opacity: 1;
    color: rgba(255, 159, 26, var(--tw-text-opacity));
  }
</style>
