<template>
  {{
    valorCrear == "SI" || valorCrear == "NO" || valorCrear == "N/A"
      ? (capturar(valorCrear).length, (valorCrear = ""))
      : ""
  }}

  <div
    @click="valores.botones = 1"
    v-if="valores.botones === 0"
    class="actividad"
    :class="{ 'disable-div': bloquear }"
  >
    <div
      class="w-6 h-6 rounded-lg flex items-center justify-center border-2 flex-none mr-2 relative"
      :class="{
        'bg-green-200 border-green-500 text-green-500': valores.estado === 1,
        'bg-yellow-200 border-yellow-500 text-yellow-500': valores.estado === 2,
        'bg-naranja1a-200 border-naranja1a-500 text-naranja1a-500': valores.estado === 3,
      }"
    >
      <h1 v-if="valores.estado === 1">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 24 24"
          fill="currentColor"
          class="w-4 h-4"
        >
          <path
            fill-rule="evenodd"
            d="M19.916 4.626a.75.75 0 01.208 1.04l-9 13.5a.75.75 0 01-1.154.114l-6-6a.75.75 0 011.06-1.06l5.353 5.353 8.493-12.739a.75.75 0 011.04-.208z"
            clip-rule="evenodd"
          />
        </svg>
      </h1>
      <h1 v-if="valores.estado === 2">NO</h1>
      <h1 class="text-xxs" v-if="valores.estado === 3">N/A</h1>

      <div
        v-if="valores.foto === 1"
        class="absolute -left-full bg-blue-200 text-blue-500 flex items-center justify-center w-4 h-4 rounded-full shadow"
      >
        <CameraIcon class="w-4" />
      </div>

      <div
        v-if="valores.tienemensaje === true"
        class="absolute -right-full bg-indigo-200 text-indigo-500 flex items-center justify-center w-4 h-4 rounded-full shadow"
      >
        <ChatAltIcon class="w-4" />
      </div>
    </div>
    <h1 class="font-bold mr-1">{{ index + 1 }}.</h1>
    <h1 class="">
      {{ array.actividad }}
      <br />
      <span v-if="valores.fotoObligatoria" class="text-red-400">(Foto Obligatoria)</span>
      <span v-if="valores.comentarioObligatoria" class="text-red-400"
        >(Comentario Obligatorio)</span
      >
    </h1>
  </div>

  <div
    v-if="valores.botones === 1"
    class="actividad"
    :class="{ 'disable-div': bloquear }"
  >
    <!-- BOTÓN SI -->
    <button @click="capturar('SI')" class="bg-green-200 text-green-500">SI/YES</button>

    <!-- BOTÓN NO -->
    <button @click="capturar('NO')" class="bg-yellow-200 text-yellow-500">NO</button>

    <!-- BOTÓN N/A -->
    <button @click="capturar('N/A')" class="bg-naranja1a-200 text-naranja1a-500">
      N/A
    </button>

    <!-- FOTO -->
    <button class="bg-blue-200 text-blue-500 z-10 relative">
      <input
        type="file"
        class="z-0 text-white opacity-0 absolute w-full"
        @change="
          valores.botones = 0;
          valores.foto = 1;
          subirFoto($event);
        "
      />
      <CameraIcon class="w-4 p-0 m-0" />
    </button>

    <!-- BOTÓN MENSAJE -->
    <button
      @click="
        valores.botones = 3;
        valores.mensaje = 1;
        valores.tienemensaje = true;
      "
      class="bg-indigo-200 text-indigo-500"
    >
      <ChatAltIcon class="w-4" />
    </button>
  </div>

  <div
    v-if="valores.botones === 3"
    class="actividad"
    :class="{ 'disable-div': bloquear }"
  >
    <input
      type="text"
      placeholder="Coment..."
      class="p-2 w-full focus:outline-none"
      v-model="valores.comentario"
    />
    <button
      @click="
        valores.botones = 0;
        valores.tienemensaje = true;
        capturar(valores.valor);
      "
      class="bg-green-200 text-green-500"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        viewBox="0 0 24 24"
        stroke-width="1.5"
        stroke="currentColor"
        class="w-4 h-4"
      >
        <path stroke-linecap="round" stroke-linejoin="round" d="M4.5 12.75l6 6 9-13.5" />
      </svg>
    </button>
    <button
      @click="
        valores.botones = 1;
        valores.tienemensaje = false;
      "
      class="bg-red-200 text-red-500"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 24 24"
        fill="currentColor"
        class="w-4 h-4"
      >
        <path
          fill-rule="evenodd"
          d="M5.47 5.47a.75.75 0 011.06 0L12 10.94l5.47-5.47a.75.75 0 111.06 1.06L13.06 12l5.47 5.47a.75.75 0 11-1.06 1.06L12 13.06l-5.47 5.47a.75.75 0 01-1.06-1.06L10.94 12 5.47 6.53a.75.75 0 010-1.06z"
          clip-rule="evenodd"
        />
      </svg>
    </button>
  </div>
</template>

<script>
import uniqid from "uniqid";
import { format } from "date-fns";
import { ref } from "@vue/reactivity";
import { useStore } from "vuex";

import { CameraIcon, ChatAltIcon } from "@heroicons/vue/outline";
import { ShieldExclamationIcon } from "@heroicons/vue/solid";

export default {
  components: { CameraIcon, ChatAltIcon, ShieldExclamationIcon },

  props: {
    crear: { type: Object, required: false },
    array: { type: Object, required: false },
    index: { type: Number, value: 0, required: true },
    valorCrear: { type: String },
  },

  setup(props) {
    const store = useStore();
    const Swal = require("sweetalert2");
    const bloquear = ref(false);

    const Toast = Swal.mixin({
      toast: true,
      padding: ".3rem",
      background: "#2c3e50",
      iconColor: "#fff",
      position: "top",
      customClass: {
        popup: "colored-toast",
      },
      width: "19rem",
      showConfirmButton: false,
      timer: 2700,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });

    const valores = ref({
      idCaptura: uniqid() + uniqid(),
      idRegistro: props.crear.idRegistro,
      idActividad: props.array.idActividad,
      idEquipo: props.crear.idActivo,
      estado: 0,
      foto: 0,
      mensaje: 0,
      comentarioObligatoria: props.array.comentarioObligatorio,
      comentario: "",
      valor: "",
      botones: 0,
      tienemensaje: false,
      urlFoto: "",
      fotoObligatoria: props.array.adjuntoObligatorio,
    });

    const capturar = async (valor) => {
      bloquear.value = true;

      if (!valor === "SI" || !valor === "NO" || !valor === "N/A") {
        Toast.fire({
          icon: "info",
          title: "Se requiere seleccionar una Opción!",
        });
        return;
      }

      const data = {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        idCaptura: valores.value.idCaptura,
        idRegistro: valores.value.idRegistro,
        idActividad: valores.value.idActividad,
        idEquipo: valores.value.idEquipo,
        action: "crearCaptura",
        valor: valor,
        comentario: valores.value.comentario,
        adjunto: valores.value.urlFoto,
        fechaCreado: format(new Date(), "yyyy-MM-dd K:m:s"),
      };

      const response = await store.dispatch("accionesUsuario", data);

      if (response.response) {
        bloquear.value = false;

        if (valores.value.comentario.length) {
          valores.value.comentarioObligatoria = false;
        }

        if (response.data.data === "SI") {
          valores.value.botones = 0;
          valores.value.estado = 1;
          valores.value.valor = "SI";
          return;
        }

        if (response.data.data === "NO") {
          valores.value.botones = 0;
          valores.value.estado = 2;
          valores.value.valor = "NO";
          return;
        }

        if (response.data.data === "N/A") {
          valores.value.botones = 0;
          valores.value.estado = 3;
          valores.value.valor = "N/A";
          return;
        }

        return;
      }

      if (!response.response) {
        Toast.fire({
          icon: "warning",
          title: "Se requiere seleccionar un Opción!",
        });
        return;
      }
    };

    const subirFoto = async (event) => {
      const valor = valores.value.valor;
      const urlFoto = uniqid() + uniqid();

      valores.value.urlFoto = urlFoto;

      const file = event.target.files[0];
      const data = new FormData();

      data.append("action", "subirFotoSabanas");
      data.append("idDestino", localStorage.getItem("idDestino"));
      data.append("idUsuario", localStorage.getItem("usuario"));
      data.append("urlFoto", urlFoto);
      data.append("file", file);

      if (
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/gif" ||
        file.type === "image/jpg"
      ) {
        const response = await store.dispatch("subirAdjunto", data);
        if (response.resp) {
          valores.value.urlFoto = response.data.urlFoto;
          valores.value.foto = 1;
          valores.value.fotoObligatoria = false;
          capturar(valor);
        }
      } else {
        Swal.fire("AVISO", "Adjunto No Permitido", "question");
      }
    };

    return { valores, capturar, subirFoto, bloquear };
  },
};
</script>

<style scoped>
.actividad {
  @apply flex flex-row items-center justify-start text-left w-full py-2 bg-white px-2 text-xs font-semibold border-b;
}

.actividad:hover {
  @apply bg-blue-100;
}

.actividad:last-child {
  @apply border-b-0 rounded-b-xl;
}

.actividad:first-child {
  @apply rounded-t-xl;
}

button {
  @apply p-2 rounded mr-2 font-bold w-1/5 flex items-center justify-center focus:outline-none;
}

button:last-child {
  @apply mr-0;
}

.bg-naranja1a-200 {
  --tw-bg-opacity: 1;
  background-color: rgba(255, 231, 198, var(--tw-bg-opacity));
}

.border-naranja1a-500 {
  --tw-border-opacity: 1;
  border-color: rgba(255, 159, 26, var(--tw-border-opacity));
}

.text-naranja1a-500 {
  --tw-text-opacity: 1;
  color: rgba(255, 159, 26, var(--tw-text-opacity));
}

.disable-div {
  pointer-events: none;
}
</style>
