<template>

  <div class="w-full bg-gris-30 h-screen flex flex-col items-center justify-start">
    <div class="w-full p-4 font-bold text-xl">
      <h1>Historial de Sábanas</h1>
    </div>
    <div class="w-full md:p-4 p-1 font-bold text-xl flex flex-row flex-wrap items-center justify-center">
      <!-- FECHA INICIAL -->
      <div class="text-sm md:mr-4 md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Fecha Inicial</h1>
        <input type="date" class="p-3 rounded shadow focus:outline-none w-full md:w-auto" v-model="filtros.fechaInicial"
          @change="obtenerRegistros()" />
      </div>
      <!-- FECHA FINAL -->
      <div class="text-sm md:mr-4 md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Fecha Final</h1>
        <input type="date" class="p-3 rounded shadow focus:outline-none w-full md:w-auto" v-model="filtros.fechaFinal"
          @change="obtenerRegistros()" />
      </div>
      <!-- HOTELES -->
      <div class="text-sm md:mr-4 md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Hotel</h1>
        <select class="p-3 rounded shadow focus:outline-none w-full md:w-auto" v-model="filtros.idHotel"
          @change=" obtenerRegistros(); obtenerSabanas(filtros.idHotel); obtenerVillas(filtros.idHotel);">
          <option value="">Seleccione Hotel</option>
          <option v-for="(item, index) in arrayHoteles" :key="index" :value="item.idHotel">{{ item.hotel }}</option>
        </select>
      </div>
      <!-- SABANAS -->
      <div class="text-sm md:mr-4 md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Sábana</h1>
        <select class="p-3 rounded shadow focus:outline-none w-full md:w-auto" v-model="filtros.idSabana"
          @change="obtenerRegistros()">
          <option value="TODOS">TODOS</option>
          <option v-for="(item, index) in arraySabanas" :key="index" :value="item.idSabana">{{ item.sabana }}</option>
        </select>
      </div>
      <!-- VILLAS -->
      <div class="text-sm md:mr-4 md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Villa</h1>
        <select class="p-3 rounded shadow focus:outline-none w-full md:w-auto" v-model="filtros.villa"
          @change="obtenerRegistros()">
          <option value="TODOS">TODOS</option>
          <option v-for="(item, index) in arrayVillas" :key="index" :value="item">{{ item }}</option>
        </select>
      </div>
      <!-- VISTAS -->
      <div class="text-sm md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Visualizar</h1>
        <select class="p-3 rounded shadow focus:outline-none w-full md:w-auto" v-model="filtros.visualizar"
          @change="obtenerRegistros()">
          <option value="0">Todo</option>
          <option value="1">Solo habitaciones con Sábanas</option>
        </select>
      </div>
    </div>
    <div class="flex flex-col items-start justify-start w-full py-1 md:py-0 overflow-x-auto h-screen "
      v-for="(item, index) in array" :key="index">
      <!-- SIN REGISTROS -->
      <div class="w-full py-12 mr-2 text-center text-2xl" v-if="!item.equipos.length">
        <h1 class="font-bold w-full">SIN REGISTROS</h1>
      </div>
      <!-- SEMANAS -->
      <div
        class="flex flex-row items-center justify-center hover:bg-gray-200 md:mt-3 mb-1.5 sticky -top-1 md:top-0 z-40 bg-gris-30"
        v-if="item.equipos.length">
        <!-- HOTELES -->
        <div
          class=" w-40 md:w-56 flex flex-row items-center justify-center bg-gray-600 font-bold text-white sticky left-0 top-0 md:top-0 z-50 p-2 rounded-r-lg"
          v-if="item.equipos.length">
          <h1 class="text-xs md:text-sm w-full truncate">{{ item.hotel }}</h1>
        </div>
        <div class="contenedor_cuadritos py-1 md:py-1.5 ml-2">
          <SemanasCuadrito v-for="(item, index) in 52" :key="index" :semana="index + 1" :cantidad="index + 1" :color="3"
            encabezado="si" :array="[]" />
        </div>
      </div>
      <!-- CONTENEDOR DE EQUIPO CON LAS 52 SEMANAS -->
      <div v-for="(equipos, index) in item.equipos" :key="index"
        class="flex flex-row items-center justify-center hover:bg-gray-200">
        <!-- HABITACIÓN -->
        <div class=" w-40 md:w-56 mr-2 sticky md:relative left-0 z-30 md:bg-transparent bg-gris-30 py-2">
          <h1 class="text-xs md:text-sm font-bold">{{ equipos.equipo }}</h1>
        </div>
        <!-- 52 SEMANAS -->
        <div class="contenedor_cuadritos">
          <SemanasCuadrito v-for="(item, index) in 52" :key="index" :semana="index + 1"
            :cantidad="equipos.registros.length" :color="0" encabezado="no" :array="equipos"
            :planificado=" equipos.planificacion.length ? equipos.planificacion[0]['semana_' + (index + 1)] : '0'" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "@vue/runtime-core"; import {
  useStore
} from "vuex"; import SemanasCuadrito from "@/components/SemanasCuadrito.vue"; import { useRoute } from "vue-router"; export default {
  components: { SemanasCuadrito },
  props: {}, setup() {
    const store = useStore(); const route = useRoute(); const fechaHoy
      = new Date(); const arrayVillas = ref([]); const filtros = ref({
        fechaInicial: fechaHoy.getFullYear()
          + "-" + (fechaHoy.getMonth() + 1 > 9 ? fechaHoy.getMonth() + 1 : "0" + (fechaHoy.getMonth()
            + 1)) + "-" + (fechaHoy.getDate() > 9 ? fechaHoy.getDate() : "0" + fechaHoy.getDate()),
        fechaFinal: fechaHoy.getFullYear() + "-" + (fechaHoy.getMonth() + 1 > 9 ? fechaHoy.getMonth()
          + 1 : "0" + (fechaHoy.getMonth() + 1)) + "-" + (fechaHoy.getDate() > 9 ? fechaHoy.getDate()
            : "0" + fechaHoy.getDate()), idHotel: "", idSabana: "TODOS", villa: "TODOS", visualizar:
          1
      }); onMounted(async () => { await obtenerRegistros(); }); const array = computed(() => { return store.state.array; }); const arrayHoteles = computed(() => {
        return store.state.arrayHoteles;
      }); const arraySabanas = computed(() => { return store.state.arraySabanas; }); const
        obtenerSabanas = async idHotel => {
          store.state.arraySabanas = []; filtros.value.idSabana = "TODOS"; let idDestino = 0; 
          // POR PARAMETRO LOCALSTORAGE 
          if (route.params.idDestino
            == 0) if (localStorage.getItem("idDestino") > 0 && localStorage.getItem("idDestino")
              != 10) idDestino = localStorage.getItem("idDestino");
          // POR PARAMETRO ROUTER 
          if (route.params.idDestino > 0) if (route.params.idDestino != 10) idDestino = route.params.idDestino;
          const response = await store.dispatch("accionesUsuario", {
            idDestino: idDestino,
            idUsuario: localStorage.getItem("usuario"), action: "obtenerSabanas", idHotel: idHotel
          }); if (response.response) store.state.arraySabanas = response.data.data;
        }; const
          obtenerVillas = async idHotel => {
            arrayVillas.value = []; filtros.value.villa =
              "TODOS"; let idDestino = 0;
               // POR PARAMETRO LOCALSTORAGE 
            if (route.params.idDestino == 0) if (localStorage.getItem("idDestino") > 0 && localStorage.getItem("idDestino") != 10) idDestino = localStorage.getItem("idDestino");
            // POR PARAMETRO ROUTER 
            if (route.params.idDestino > 0) if (route.params.idDestino != 10) idDestino = route.params.idDestino;
            const response = await store.dispatch("accionesUsuario", {
              idDestino: idDestino,
              idUsuario: localStorage.getItem("usuario"), action: "obtenerVillas", idHotel: idHotel
            }); if (!response.response) { arrayVillas.value = []; return; } if (response.response) {
              response.data.data.forEach(element => {
                if (arrayVillas.value.indexOf(element)
                  === -1) { arrayVillas.value.push(element); }
              }); return;
            }
          };
    const obtenerRegistros = async () => {
      store.state.array = [];
      let idDestino = 0;
      // POR PARAMETRO LOCALSTORAGE
      if (route.params.idDestino == 0) if (localStorage.getItem("idDestino") > 0 && localStorage.getItem("idDestino")
        != 10) idDestino = localStorage.getItem("idDestino");
      // POR PARAMETRO ROUTER 
      if (route.params.idDestino > 0) if (route.params.idDestino != 10) idDestino = route.params.idDestino;
      await store.dispatch("getArray", {
        idDestino: idDestino, idUsuario: localStorage.getItem("usuario"),
        action: "semanas", fechaInicial: filtros.value.fechaInicial, fechaFinal: filtros.value.fechaFinal,
        idSabana: filtros.value.idSabana, idHotel: filtros.value.idHotel, villa: filtros.value.villa,
        visualizar: filtros.value.visualizar
      });
    }; return { obtenerRegistros, array, arrayHoteles, obtenerSabanas, arraySabanas, obtenerVillas, arrayVillas, filtros };
  }
};
</script>

<style scoped>
.contenedor_cuadritos {
  display: grid;
  grid-template-columns: repeat(52, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
</style>

